<!--  -->
<template>
  <div class="">
    <!-- <el-table :data="tableData" border style="width: 100%">
      <el-table-column label="交换机名称" prop="switchName" width="100px">
      </el-table-column>
      <el-table-column label="dpid" prop="dpid"> </el-table-column>
      <el-table-column type="expand">
          <el-table :data="ports1" stripe style="width: 100%">
            <el-table-column prop="dpid" label="dpid" width="180">
            </el-table-column>
            <el-table-column prop="port_no" label="端口号" width="180">
            </el-table-column>
            <el-table-column prop="hw_addr" label="地址"> </el-table-column>
            <el-table-column prop="name" label="端口名称"> </el-table-column>
          </el-table>
      </el-table-column>
    </el-table> -->
    <el-collapse v-model="activeNames" style="margin-left: 15px">
      <el-collapse-item name="1">
        <template slot="title">
          <p class="title">
            {{ tableData[0].switchName }} > {{ tableData[0].dpid }}
          </p>
        </template>
        <div>
          <el-table
            :data="ports1"
            border
            style="width: 100%"
            :header-cell-style="{
              'background-color': 'rgba(32, 39, 65, 0.7)',
              color: '#fff',
            }"
          >
            <el-table-column prop="dpid" label="dpid"> </el-table-column>
            <el-table-column prop="hw_addr" label="hw_addr"> </el-table-column>
            <el-table-column prop="name" label="端口名称"> </el-table-column>
            <el-table-column prop="port_no" label="端口号"> </el-table-column>
          </el-table>
        </div>
      </el-collapse-item>
      <el-collapse-item name="2">
        <template slot="title">
          <p class="title">
            {{ tableData[1].switchName }} > {{ tableData[1].dpid }}
          </p>
        </template>
        <div>
          <el-table
            :data="ports2"
            border
            style="width: 100%"
            :header-cell-style="{
              'background-color': 'rgba(32, 39, 65, 0.7)',
              color: '#fff',
            }"
          >
            <el-table-column prop="dpid" label="dpid"> </el-table-column>
            <el-table-column prop="hw_addr" label="hw_addr"> </el-table-column>
            <el-table-column prop="name" label="端口名称"> </el-table-column>
            <el-table-column prop="port_no" label="端口号"> </el-table-column>
          </el-table>
        </div>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      tableData: [],
      ports1: [],
      ports2: [],
      activeNames: ["1", "2"],
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    loadSwiches() {
      this.$axios
        .get(this.$httpUrl + "/inf/switchesInf")
        .then((res) => res.data)
        .then((res) => {
          // console.log(res);
          res[0].switchName = "交换机1";
          res[1].switchName = "交换机2";
          // console.log(res[0]);
          this.tableData = res;
          console.log(this.tableData.ports);
          this.ports1 = this.tableData[0].ports;
          this.ports2 = this.tableData[1].ports;
          // console.log(this.tableData);
          console.log(this.ports1);
        });
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.loadSwiches();
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style  lang="scss" scoped>
.title {
  font-size: 20px;
}
</style>